@import "./variables.less";
@import "./fonts.less";

.partnership {
  padding: 26px 20px;
  display: flex;
  justify-content: space-between;
}

.partnership__title {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.partnership__adress {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: @specialgrey;
}
