@import "./variables.less";
@import "./fonts.less";

.promotion {
  width: 280px;
  background-color: @green;
  background-image: url("/source/img/pictures/catalog-promotion-background-small.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.promotion__content {
  margin: 0;
  padding: 187px 61px 43px 58px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: @white;
  text-align: center;
}
